<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" style="margin-bottom: 50px;">
      <van-search v-model="search" placeholder="请输入搜索关键词" />
      <!--banner-->
      <van-swipe :autoplay="3000">
        <van-swipe-item v-for="(image, index) in images" :key="index">
          <img v-lazy="image" width="100%" height="200px" />
        </van-swipe-item>
      </van-swipe>
      <!--notice-->
      <van-notice-bar left-icon="volume-o" :scrollable="false">
        <van-swipe vertical class="notice-swipe" :autoplay="3000" :show-indicators="false">
          <van-swipe-item v-for="(item, index) in notice" :key="index" @click="navto(item.id)">{{item.title}}
          </van-swipe-item>
        </van-swipe>
      </van-notice-bar>
      <!--menu-->
      <van-grid :column-num="4" :icon-size="40">
        <van-grid-item v-for="(item, index) in menulist" :key="index" :icon="item.icon" :text="item.title"
          @click="navto(item.url)" />
      </van-grid>

      <!--goods-->
      <van-cell title="推荐商品" is-link url="#/goods" value="查看更多" />
      <van-row gutter="20" class="main_list">
        <van-col span="12" v-for="(item, index) in list" :key="index">
          <img :src="item.img" height="150px" width="100%">
          <span class="goods_desc van-multi-ellipsis--l2">
            {{item.title}}
          </span>
        </van-col>
      </van-row>
    </van-pull-refresh>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        count: 0,
        isLoading: false,
        loading: false,
        finished: false,
        refreshing: false,
        search: '',
        images: [
          'http://zhongjun.ueoa.net/static/uploads/images/2021/08/02/16279139176107febd3c8b9.png',
          'http://zhongjun.ueoa.net/static/uploads/images/2021/08/02/16279078696107e71d8eafa.jpg',
        ],
        notice: [{
            'id': 1,
            'title': "新闻一"
          },
          {
            'id': 2,
            'title': "新闻二"
          }
        ],
        menulist: [{
            icon: '//m.360buyimg.com/mobilecms/s120x120_jfs/t1/175540/24/19329/6842/60ec0b0aEf35f7384/ec560dbf9b82b90b.png!q70.jpg.dpg',
            title: '手机',
            url: '/goods'
          },
          {
            icon: 'gift-o',
            title: '电脑',
            url: '/goods'
          },
          {
            icon: 'gift-o',
            title: '平板',
            url: '/goods'
          },
          {
            icon: 'gift-o',
            title: '手表',
            url: '/goods'
          },
        ],
        list: [{
            title: '江西三清山婺源3日自由行定制',
            img: 'http://zhongjun.ueoa.net/static/uploads/images/2021/08/02/1627914790610802263dac1.jpeg',
          },
          {
            title: '江西三清山婺源3日自由行定制',
            img: 'http://zhongjun.ueoa.net/static/uploads/images/2021/08/02/1627914790610802263dac1.jpeg',
          },
          {
            title: '江西三清山婺源3日自由行定制',
            img: 'http://zhongjun.ueoa.net/static/uploads/images/2021/08/02/1627914790610802263dac1.jpeg',
          },
          {
            title: '江西三清山婺源3日自由行定制',
            img: 'http://zhongjun.ueoa.net/static/uploads/images/2021/08/02/1627914790610802263dac1.jpeg',
          },
          {
            title: '江西三清山婺源3日自由行定制',
            img: 'http://zhongjun.ueoa.net/static/uploads/images/2021/08/02/1627914790610802263dac1.jpeg',
          },
          {
            title: '江西三清山婺源3日自由行定制',
            img: 'http://zhongjun.ueoa.net/static/uploads/images/2021/08/02/1627914790610802263dac1.jpeg',
          }
        ]
      };
    },

    methods: {
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
          this.count++;
        }, 1000);
      },
      //封装跳转
      navto(id) {
        this.$router.push({
          name: 'goods',
          params: {
            id: 1
          }
        })
      }
    }
  }
</script>

<style>
  .my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
  }

  .notice-swipe {
    height: 40px;
    line-height: 40px;
  }

  .main_list {
    width: 96%;
    padding: 2%;
  }

  .goods_desc {
    width: 100%;
    height: auto;
    line-height: 25px;
    display: inline-block;
  }
</style>



